import React, { Component } from 'react'
import {Link} from "react-router-dom"
import logo from "../images/logo.jpg"
export class navbar extends Component {
    render() {
        return (
            <div className="navbar navbar-expand-md navbar-dark bg-light" id="navbar">
        <div className="navbar-brand">
          <Link to="/portfolio"><img className="logo rounded-circle" src={logo} alt="" /></Link>
          <h1 className="navbar-brand ml-3" style={{color:"white"}}>Sumit Kumar</h1>
        </div>
        <ul className="navbar-nav w-100 float-right">
          <li className="navbar-item">
            <Link className=" col-2 nav-link lin" to="/portfolio/projects">
              Projects
            </Link>
          </li>
          <li className="navbar-item">
            <Link className="nav-link lin" to="/about">
              About
            </Link>
          </li>
          <li className="navbar-item">
            <Link className="nav-link lin" to="/contact">
              Contact Us
            </Link>
          </li>
        </ul>
      </div>
        )
    }
}

export default navbar
